@media print {
  /* your print styles */
  .raleigh-campaign.Campaign.CampaignType--floating div,
  .site-header .header-right-column,
  #page .site-main .post-nav-wrapper,
  #page .site-main .social-share-wrapper,
  aside.sidebar .location-callout-box-basic,
  aside.sidebar .featured-posts-block,
  aside.sidebar .categories-block,
  #page .site-main .call-to-action,
  footer.primary-footer {
    display: none;
  }
}

main {
  padding-bottom: 0 !important;
}

.text-white > * {
  color: white;
}

h3:before {
  color: #177e89;
  content: "//";
  font-size: 3.5rem;
  left: -1rem;
  position: relative;
  letter-spacing: -5px;
}

.entry-title {
  text-align: left;
}

.entry-title:before {
  color: #177e89;
  content: "//";
  font-size: 3.5rem;
  left: -1rem;
  position: relative;
  letter-spacing: -5px;
}

@media (max-width: 767.98px) {
  .entry-title:before {
    display: none !important;
  }
}

/**************************************
***			Hero Section			***
**************************************/
.hero-loc {
  position: relative;
  padding: 65px 0;
}

.hero-loc a {
  color: #fff;
}

@media (max-width: 767.98px) {
  .hero-loc {
    padding: 50px 0;
    padding-top: 120px !important;
  }
}

.hero-loc:not(.no-video) {
  background-image: url("../../img/pattern/patt_4_blue.svg") !important;
  background-repeat: repeat;
  background-size: 30px auto;
}

.hero-loc:not(.no-video):before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: #084C61;
  z-index: -1;
}

.hero-loc.no-video:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(105deg, #063f51 calc(50% - 60px), rgba(0, 57, 57, 0.2) calc(50% + 75px));
}

.hero-loc.no-video:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 60%;
  height: 100%;
  z-index: -1;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center 25%;
}

@media (max-width: 767.98px) {
  .hero-loc.no-video:before {
    background: linear-gradient(10deg, #063f51 calc(50%), rgba(0, 57, 57, 0.2) calc(50% + 75px));
  }
  .hero-loc.no-video:after {
    width: 100%;
    height: 60%;
  }
  .hero-loc.no-video .container .row {
    flex-direction: column-reverse;
  }
}

.hero-loc .container {
  max-width: 1280px !important;
}

.hero-loc .container .row {
  align-items: center;
}

@media (min-width: 576px) {
  .hero-loc .container .row .col-sm-6:first-of-type {
    padding-right: 60px;
  }
}

.hero-loc .container h2 {
  margin-bottom: 25px;
  color: #fff;
  font-weight: bold;
}

.hero-loc .container p {
  color: #fff;
}

.hero-loc .container ul {
  padding-left: 0;
  margin-top: 30px;
  list-style: none;
}

.hero-loc .container ul li {
  color: #fff;
  margin-bottom: 15px;
}

.hero-loc .container ul li.links-title {
  font-weight: 500;
}

.hero-loc .container ul li:not(.links-title):before {
  content: "\00BB \0020";
  vertical-align: text-bottom;
}

.hero-loc .container ul li a {
  font-weight: normal;
  color: #fff;
}

.hero-loc .container ul li a:hover {
  text-decoration: underline;
}
